body {
  text-align: center;
  background-color: #f4f4f4;
  padding: 20px;
  font-family: Arial, sans-serif;
}

h1, h2 {
  color: #333;
}

button {
  cursor: pointer;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 10px;
  padding: 10px 20px;
  font-size: 18px;
  transition: background .3s;
}

button:hover {
  background-color: #0056b3;
}

#hourTimerMessage, #secondTimerMessage {
  color: #d9534f;
  margin-top: 20px;
  font-size: 24px;
  font-weight: bold;
}
